.section-tradition {
    padding-top: 125px;
  
    background-color: getColor('lightPink');
    background-image: url('../images/tradition/sectionbg1.png');
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 0px 705px;
  
    @media screen and (min-width: 768px) {
      padding-top: 130px;
  
      background-position: 0px 350px;
    }
  }
  
  .tradition-subtitle {
    margin-top: 0;
    margin-bottom: 15px;
  
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
  
    color: getColor('secondaryPink');
  
    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
  
      font-size: 26px;
      line-height: 1.15;
    }
  }
  
  .tradition-title {
    margin-top: 0;
    margin-bottom: 26px;
  
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.18;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    color: getColor('accent');
  
    @media screen and (min-width: 768px) {
      margin-bottom: 50px;
  
      font-size: 48px;
      line-height: 1.15;
    }
  }
  
  .tradition-first-box {
    @media screen and (min-width: 768px) {
      display: flex;
    }
  }
  
  .tradition-img {
    display: block;
    width: 100%;
  
    @media screen and (min-width: 768px) {
      width: 50%;
      height: 339px;
    }
  }
  
  .tradition-text-box {
    padding-left: 30px;
    padding-right: 30px;
  
    font-family: 'DM Sans', sans-serif;
  
    @media screen and (min-width: 768px) {
      padding-left: 17px;
      padding-right: 17px;
    }
  }
  
  .tradition-text {
    margin-top: 0;
    margin-bottom: 0;
  
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
  
    color: getColor('secondaryText');
  
    &.tradition-text--accent {
      margin-bottom: 38px;
  
      color: getColor('mainText');
    }
  
    &.traditiom-text--margin {
      margin-bottom: 10px;
    }
  
    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.76;
    }
  }
  
  .tradition-btn {
    margin-top: 17px;
    padding: 9px 37px;
  
    border: none;
    border-radius: 22px;
  
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;
  
    color: getColor('accent');
    background-color: getColor('main-bg');
  
    @media screen and (min-width: 768px) {
      margin-top: 62px;
  
      font-size: 16px;
      line-height: 1.31;
    }
  }
  
  .tradition-list {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    padding-top: 39px;
    padding-bottom: 97px;
  
    list-style: none;
  
    @media screen and (min-width: 768px) {
      padding-top: 66px;
      padding-bottom: 140px;
  
      display: flex;
      flex-wrap: nowrap;
    }
  }
  
  .tradition-list__item:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 38px;
    }
  
    @media screen and (min-width: 768px) {
      margin-right: 60px;
    }
  }
  
  .tradition-list__number {
    margin-top: 0;
    margin-bottom: 19px;
  
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    color: getColor('accent');
  
    @media screen and (min-width: 768px) {
      font-size: 48px;
      line-height: 1.15;
      text-transform: lowercase;
    }
  }
  
  .tradition-list__text {
    margin-top: 0;
    margin-bottom: 0;
  
    font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.7;
  
    color: getColor('mainText');
  
    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.74;
    }
  }
